.main{
  background: #fff;
  padding: 30px;
  margin: 50px auto;
  max-width: 500px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 1);


}

.main h1{
   font-size: 20px;
   text-align: center;
}
.form{
  margin-top: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.form input[type='text']{
  height: 40px ;
  padding: 0 20px ;
  font-size: 20px;
  border: 1px solid #ccc;
  min-width: 80%;
}
.form button[type='submit']{
  border: none;
  height: 40px;
  width: 40px;
  background: #f04c64;
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
}
.tarefas{
  margin-top: 30px;
}
.tarefas li{
display: flex;
align-items: center;
justify-content: space-between;
padding: 10px;
transition: all 550ms;
}
.tarefas li:hover{
  background:#51c5de ;
  }


.tarefas li .edit{
  margin-right: 15px;
  color: #51c5de;
  cursor: pointer;
  transition: all 550ms;

}
.tarefas li .delete{
  margin-right: 15px;
  color: #f04c64;
  cursor: pointer;
  transition: all 550ms;

}
.tarefas li:hover .edit,.tarefas li:hover .delete{
  color: #000;
}
.tarefas li span{
  display: flex;
  align-items: center;

}

